body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.game {
  margin: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(100, 10px);
}

.cell {
  background-color: white;
  color: white;
  border: 0px solid white;
  width: 10px;
  height: 10px;
}

.cell.active {
  background-color: black;
}



